.element{
    display: inline-block;
    float: right;
}
@media (max-width: 1200px) {
    .element{
        display: block;
        float:none;
    }
}
.label{
    margin-right: 0.5em;
    margin-top: 0.3em;
}
@media (max-width: 600px){
    .label{
        display: block;
    }
}
.cal{
    position: absolute;
    margin-top: 0.1em;
    border: solid 1px #333333;
    background: white;
    padding: 1em;
}
.header input{
    margin-left: 0;
    margin-right: 0;
}
.header button{
    margin-left: 0;
    margin-right: 0;
}
.cal table{
    display: inline-block;
    margin-bottom: 0;
}
td:empty{
    background: #fff !important;
}
tbody td{
    font-size: 1.2em;
}
.selector{
    margin-top: 0.2em;
    margin-left: 1em;
    display: inline-block;
    vertical-align: top;
    font-size: 1.2em;
}
.selector h3{
    margin-top: 0;
    margin-bottom: 0.5em;
}
.selected.targeted{
    background: #68abad;
}
.targeted{}